<template>
    <div class="column"><!--栏目-->
        <List3 :list3="list3" dataName="column"></List3>
    </div>
</template>

<script>
import List3 from '@/components/List3';
export default {
  data(){
    return {
      list3:[]
    } 
  },
  components:{List3},
  mounted(){
     axios({
      url:'/api/column',
      params:{_page:1,_limit:6} 
    }).then(
      res=>this.list3 = res.data.data
    )
  }
}
</script>