<template>
    <div class="home"><!--首页-->
        <Slider></Slider>
        <List2></List2>
        <List :list="list" dataName="home" ></List>
    </div>
</template>

<script>
import Slider from '@/components/Slider'
import List2 from '@/components/List2'
import List from '@/components/List'

export default {
        name:'home',
    data(){
        return {
        list:[],
        banner:[]
        }
    },
    components:{Slider,List2,List},

    mounted(){
    // console.log(this.$axios)
    // console.log(axios)

    axios({
      url:'/api/home',
      params:{_page:1,_limit:4}
    }).then(
      res=>{
        this.list = res.data.data
      }
    )
}
}
</script>
