<template>
    <div class="spxq">
        <div class="nav">
            <ul>
                <li class="l-btn" @click="goback"></li>
            </ul>
        </div>
        <ul class="xq">
            <li>
                <div class=''>
                <img :src="`${baseUrl}${spxq.auth_icon}`"/>
                </div>
                <span>{{spxq.title}}</span>
                <p>[3个工作日发货]</p>
                <em>{{spxq.dex}}</em>
                <div class="spxq">商品详情</div>
                <p class="px">{{spxq.xq}}</p>
            </li>
        </ul>
        <div class="foot">
          <div class="gw">{{spxq.gw}}</div>
          <div class="jr">{{spxq.jr}}</div>
          <div class="lj">{{spxq.lj}}</div>
        </div>

    </div>
</template>

<script>
    export default {
  data(){
    return {
      spxq:{}
    }
  },
  methods:{
    goback(){
      this.$router.go(-1)
    }
  },
  /* filters:{
    date(){
      return '时间'
    }
  }, */
  created(){
    // console.log('detail,created,baseUrl',this.baseUrl)
    let id = this.$route.params.id;
    let dataName = this.$route.query.dataName;
    // console.log(id,dataName)
    axios({
      url:`/api/${dataName}/${id}`,
    }).then(
     
      res=>{
        this.spxq= res.data.data
         console.log(this.spxq)
      }
    )
  }
}
</script>


