<template>
    <div class="slider"><!--轮播图-->
        <ul>
            <li><img src="/images/banner1.jpg" alt=""></li>
            <li><img src="/images/banner2.jpg" alt=""></li>
            <li><img src="/images/banner3.jpg" alt=""></li>
        </ul>
    </div>
</template>

<script>
import Swipe from 'swipe';
import $ from 'jquery';
export default {
    mounted(){
        new Swipe($('.slider')[0],{
            auto:2000,
            continuous:true,
            stopPropation:true,
            callback:function(index,element){
                $('.slider ol li').removeClass('active');
                $('.slider ol li').eq(index).addClass('active');
            }
    })
    }
}
</script>

<style scoped>
.slider{width:100%;height:4.5rem;overflow: hidden;}
.slider ul li{width:6.4rem;float:left}
.slider ul li img{width:100%;height:4.5rem;display:block}
</style>

