<template>
    <div class="detail"><!--详情-->
        <div class="nav">
            <ul>
                <li class="l-btn" @click="goback"></li>
            </ul>
        </div>
        <div class="xq" v-if="detail.detail">
            <h3>{{detail.title}}</h3>
            <p v-html="detail.detail.auth"></p>
            <div class="tu"><img :src="`${baseUrl}${detail.detail.auth_icon}`" alt=""></div>
            <p v-html="detail.detail.content"></p>
        </div>
    </div>
</template>

<script>
export default {
  data(){
    return {
      detail:{}
    }
  },
  methods:{
    goback(){
      this.$router.go(-1)
    }
  },
  /* filters:{
    date(){
      return '时间'
    }
  }, */
  created(){
    // console.log('detail,created,baseUrl',this.baseUrl)
    let id = this.$route.params.id;
    let dataName = this.$route.query.dataName;
    // console.log(id,dataName)
    axios({
      url:`/api/${dataName}/${id}`,
    }).then(
      res=>this.detail = res.data.data
    )
  }
}
</script>

<style scoped>
html,body{ overflow-x: hidden; }
  .nav{width:100%; position:fixed;top:0;left:0; background:#fff; border-bottom:1px solid #e8eaec; z-index:99;}
  .nav ul{width:6.4rem;height:0.45rem; padding-top:0.15rem; margin:0 auto;}
  .nav ul li{width:0.29rem;height:0.31rem; background:url(../assets/img/history.png) no-repeat 0 0 ; background-size:100%; margin:0 0 0 0.2rem;}

  .detail .xq{width:100%;padding-top:.6rem;}
  .detail .xq h3{margin-left:.2rem;}
  .detail .xq p{font:.16rem/.32rem "";color:#ccc;text-indent:2em;word-wrap:break-word;}
</style>

