<template>
  <div class="content">
    <div class="login-box"> 
      <div class="login">
        <router-link to="/login">登录页面</router-link>
      </div>  
    </div>
    <ul>
      <li class="lifirst">
        <input type="text" v-model="username" placeholder="请输入账号"/>
        <span>帐号</span>
      </li>
      <li>
        <input type="password" v-model="password" placeholder="请输入密码"/>
        <span>密码</span>
      </li>
      <li>
        {{mess}}
      </li>
    </ul>
    <div class="footbox">
      <input type="button" value="登 录" class="login-btn" @click="login" />
      <!-- <a href="javascript:;" class="tishi" @click="reg">忘记密码？</a> -->
      <router-link to="/reg" >注册</router-link>
    </div>
  </div>
</template>
<script>
export default {
  data(){
    return {
      username:'',
      password:'',
      mess:''
    }
  },
  methods:{
    login(){
      axios({
        url:'/api/login',
        method:'post',
        data:{username:this.username,password:this.password}
      }).then(
        res => {
          // console.log('1',res);
          if(res.data.err===0){
            this.$router.push('/user')
          }else{
            this.mess = res.data.msg
          }
        }
      )
    }
  }
}
</script>

<style scoped>
body{ background:#f2f4f5;}
.content .login-box{width:100%;height:.6rem;background:rgb(226, 233, 236);}
.content .login{width:1.5rem;height:0.3rem; margin:0 auto;}
.login a{width:1.5rem;height:0.3rem; font-size:0.3rem; line-height:0.3rem; float:left; color:#676868; text-align:center;margin-top:.15rem;}
.content .login-box{ position:relative;}
.content ul{width:5.78rem;height:1.92rem; border:1px solid #e5e7e8; background:#fff; margin:0 auto; margin-top:0.2rem;}
.content ul li{height:0.95rem; position:relative;}
.content ul li.lifirst{border-bottom:1px solid #e5e7e8;}
.content ul li input{ padding-left:1.24rem;width:4.54rem;height:100%; font-size:0.2rem;}
.content ul li span{width:1.23rem;height:0.49rem; position:absolute;top:0.2rem;left:0;  color:#676868; font-size:0.25rem; line-height:0.49rem; text-align:center;}
.content .footbox{width:4.65rem;height:0.65rem;margin:0 auto; }
.content .login-btn{width:4.65rem;height:0.65rem; background:rgb(255, 102, 0); color:#fff; text-align:center; line-height:0.65rem; border-radius:8px; display:block; margin-top:0.62rem; font-size:0.28rem;}
.content .footbox a{width:1.4rem; font-size:0.25rem; margin-top:0.28rem; display:block; color:rgb(255, 102, 0);}

</style>
