<template>
    <div class="follow"><!--关注-->
        <List :list="list" dataName="follow"></List>
    </div>
</template>

<script>
import List from '@/components/List';
export default {
  data(){
    return {
      list:[]
    } 
  },
  components:{List},
  mounted(){
     axios({
      url:'/api/follow',
      params:{_page:1,_limit:6} 
    }).then(
      res=>this.list = res.data.data
    )
  }
}

</script>

